import React from 'react'
import {
  Box,
  Container,
  Divider,
  Flex,
  Image,
  Text,
  UnorderedList,
  ListItem,
  VStack,
} from '@chakra-ui/react'

export default function ExperienceCard({ data }) {
  const { company, location, role, time, description, image } = data
  return (
    <Flex height="250px">
      <VStack alignItems="flex-end" width="225px" mr="2rem" spacing="0.5" fontSize="md">
        <Image boxSize="100px" src={image} />
        <Text fontWeight="bold">{company}</Text>
        <Text>{location}</Text>
        <Text>{time}</Text>
        <Text>{role}</Text>
      </VStack>

      <Divider orientation="vertical" />

      <Flex pl="1rem" maxW="375px">
        <UnorderedList>
          {description.map((text) => (
            <ListItem>{text}</ListItem>
          ))}
        </UnorderedList>
      </Flex>
    </Flex>
  )
}
